<template>
<div class="region-detail">
	<div class="page-header">
		<mt-header class="container" title="Time Sheet Detail">
			<router-link :to="{name: 'RegionTimesheetList'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
			<span slot="right" @click="onSave">
				<font-awesome-icon icon="save" size="2x" />
			</span>
		</mt-header>
	</div>
	
	<div class="page-body container">
		<mt-field disabled label="DateTime" type="text" readonly="readonly" v-model="entity.DateTime"></mt-field>
		<mt-field disabled label="JobNumber" type="text" readonly="readonly" v-model="entity.JobNumber"></mt-field>
		<mt-field disabled label="FirstName" type="text" readonly="readonly" v-model="entity.FirstName"></mt-field>
		<mt-field disabled label="LastName" type="text" readonly="readonly" v-model="entity.LastName"></mt-field>

		<c-time-picker field-label="PostIn" :field-value="entity.PostIn"  @selectedChanged="onPostInChanged"></c-time-picker>
		<c-time-picker field-label="PostOut" :field-value="entity.PostOut"  @selectedChanged="onPostOutChanged"></c-time-picker>
		<c-time-picker field-label="CheckIn" :field-value="entity.CheckIn"  @selectedChanged="onCheckInChanged"></c-time-picker>
		<c-time-picker field-label="CheckOut" :field-value="entity.CheckOut"  @selectedChanged="onCheckOutChanged"></c-time-picker>

       <div v-if="hasPhotoIn" class="photo">
			<p>{{photoInTitle}}</p>
			<img :src="'data:image/jpeg;base64,' + entity.PhotoIn" />
		</div>
	
		<div v-if="hasPhotoOut" class="photo">
			<p>{{photoOutTitle}}</p>
			<img :src="'data:image/jpeg;base64,' + entity.PhotoOut" />
		</div>

		<mt-field disabled label="Break1In" type="text" readonly="readonly" v-model="entity.Break1In"></mt-field>
		<mt-field disabled label="Break1Out" type="text" readonly="readonly" v-model="entity.Break1Out"></mt-field>
		<mt-field disabled label="Break2In" type="text" readonly="readonly" v-model="entity.Break2In"></mt-field>
		<mt-field disabled label="Break2Out" type="text" readonly="readonly" v-model="entity.Break2Out"></mt-field>
		<mt-field disabled label="Break3In" type="text" readonly="readonly" v-model="entity.Break3In"></mt-field>
		<mt-field disabled label="Break3Out" type="text" readonly="readonly" v-model="entity.Break3Out"></mt-field>

		<mt-button size="large" type="danger" @click="onDelete"><font-awesome-icon icon="trash"></font-awesome-icon></mt-button>
	</div>
</div>
</template>

<script>
import BizRCHub from '../../api/BizRCHub.js'
import Utils from '../../utils/index.js';

import TimePicker from '../../components/TimePicker.vue';

export default {
	name: 'RegionTimesheetDetail',
	components:{
		'c-time-picker': TimePicker
	},
	data(){
		return {
			entity: {
				PostIn: '',
				PostOut: '',
				CheckIn: '',
				CheckOut: ''
			}
		}
	},
	computed:{
		hasPhotoIn()
		{
			if(!this.entity || !this.entity.PhotoIn)
				return false;

			return this.entity.PhotoIn.length > 0;
		},
		photoInTitle()
		{
			if(!this.entity || !this.entity.PhotoInSize)
				return "Photo In";

			return "Photo In(" + this.entity.PhotoInSize + ')';
		},
		hasPhotoOut()
		{
			if(!this.entity || !this.entity.PhotoOut)
				return false;

			return this.entity.PhotoOut.length > 0;
		},
		photoOutTitle()
		{
			if(!this.entity || !this.entity.PhotoOutSize)
				return "Photo Out";

			return "Photo Out(" + this.entity.PhotoOutSize + ')';
		},
	},
	methods:{
		onSave()
		{
			let modelJson = JSON.stringify(this.entity);	
			this.$showLoading();
			BizRCHub.UpdateRegionTimeSheet(modelJson).then(res=>{
				this.$closeLoading();
				if(res.data.OK)
				{
					let data = res.data.Result;
					for(let item of this.$store.state.regionTimeSheetList)
					{
						if(item.HRTimeSheetPK == this.entity.HRTimeSheetPK)
						{
							Object.assign(item, data);
							break;
						}
					}	

					this.$router.push({name: 'RegionTimesheetList'});
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});	
		},
		onDelete()
		{
			this.$confirm('Are you sure to delete this item?', 'Question').then(action=>{
				this.$showLoading();
				BizRCHub.DeleteRegionTimeSheet(this.entity.HRTimeSheetPK).then(res=>{
					this.$closeLoading();
					if(res.data.OK)
					{
						let list = this.$store.state.regionTimeSheetList;
						for(let i = list.length - 1;i >= 0; i--)
						{
							if(list[i].HRTimeSheetPK == this.entity.HRTimeSheetPK)
							{
								list.splice(i, 1);
								break;
							}
						}	

						this.$router.push({name: 'RegionTimesheetList'});
					}
					else
					{
						this.$alert(res.data.Error, 'Error');
						console.log(res.data.Error);
					}
				}).catch(err=>{
					this.showNetworkError();
					console.log(err);
					this.$closeLoading();
				});
			}).catch(err=>{});
		},
		onPostInChanged(time)
		{
			this.entity.PostIn = time;
		},
		onPostOutChanged(time)
		{
			this.entity.PostOut = time;
		},
		onCheckInChanged(time)
		{
			this.entity.CheckIn = time;
		},
		onCheckOutChanged(time)
		{
			this.entity.CheckOut = time;
		}
	},
	mounted()
	{
		let PK = this.$route.params.PK;
		
		this.$showLoading();
		BizRCHub.GetRegionTimeSheet(PK).then(res=>{
			if(res.data.OK)
			{
				this.entity = res.data.Result;
			}
			else
			{
				this.$alert(res.data.Error, 'Error');
				console.log(res.data.Error);
			}
			
			this.$closeLoading();
		}).catch(err=>{
			this.showNetworkError();
			console.log(err);
			this.$closeLoading();
		});
	}
}
</script>

<style scoped>
	.page-header {
		background-color: #a9362d;
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 100;
	}
	.mint-header {
		background-color: #a9362d;
	}
	.page-body{
		margin-top: 40px;
	}
	.mint-button--danger {
		background-color: #a9362d;
	}
</style>
<style>
	.region-detail .mint-field-core {
		background-color: transparent;
	}
	.region-detail .mintui.mintui-field-error {
		display: none;
	}
</style>

