<template>
<div class="region-list">
	<div class="page-header">
		<mt-header class="container" :title="manager.SearchRegionName">
			<span slot="left" @click="onShowMenu">
				<font-awesome-icon icon="bars" size="2x" />
			</span>
		</mt-header>
	</div>

	<div class="page-body container">

		<div class="level-0" v-for="level0 in listLevel" :key="level0.LevelName">
			<mt-button size="large" @click="onLevelClick(level0)" ><span class="level-0-title">{{level0.LevelName}}</span><span class="level-0-icon"><font-awesome-icon :icon="level0.Icon" /></span></mt-button>
			<div class="level-1" v-show="level0.ShowChildren" v-for="level1 in level0.Children" :key="level1.LevelName">
				<mt-button size="large" @click="onLevelClick(level1)" ><span class="level-1-title">{{level1.LevelName}}</span><span class="level-1-icon"><font-awesome-icon :icon="level1.Icon" /></span></mt-button>
				<div class="level-2" v-show="level1.ShowChildren" v-for="item in level1.Children" :key="item.HRTimeSheetPK">
					<mt-cell>
						<span slot="title" @click="onDetail(item.HRTimeSheetPK)">
							<span class="dateTime">{{item.FirstName + ' ' + item.LastName}}</span>
							<br />
							<span class="postInOut"><font-awesome-icon icon="calendar" />{{item.PostIn + ' - ' + item.PostOut}}</span>
							<br />
							<span class="checkInOut"><font-awesome-icon icon="clock" />{{item.CheckIn + ' - ' + item.CheckOut}}</span>
						</span>
					</mt-cell>
				</div>
			</div>
		</div>

		<mt-popup class="menu" v-model="popupMenu.visible" position="left">
			<ul>
				<li @click="onJob">
					<font-awesome-icon icon="building" />
					<span>Job</span>
				</li>
				<li @click="onEquipmentRepair">
					<font-awesome-icon icon="hammer" />
					<span>Repair</span>
				</li>
				<li @click="onLogout">
					<font-awesome-icon icon="sign-out-alt" />
					<span>Louout</span>
				</li>
			</ul>
		</mt-popup>

		<mt-popup class="search" ref="search" v-model="popupSearch.visible">

			<mt-field label="Region Name" placeholder="please enter here" type="text" v-model="popupSearch.region"></mt-field>
			
			<div class="button-group">
			<mt-button size="large" type="primary" @click="onSearch"><font-awesome-icon icon="search"></font-awesome-icon></mt-button>
			<mt-button size="large" type="default" @click="onSearchCancel"><font-awesome-icon icon="times"></font-awesome-icon></mt-button>	
			</div>

			<div v-for="item in popupSearch.regionList" :key="item.CoRegionPK">
				<mt-cell>
					<span slot="title">
						<span>{{item.RegionName}}</span>
					</span>
					<span @click="onUseRegion(item.CoRegionPK, item.RegionName)"><font-awesome-icon icon="check"></font-awesome-icon></span>
				</mt-cell>
			</div>	
		</mt-popup>
	</div>

	<div class="tabbar-wrapper">
	<mt-tabbar class="container">
		<mt-tab-item> 
			<span @click="onViewByDate">
				<font-awesome-icon icon="calendar-alt" size="2x" />
			</span>
		</mt-tab-item>
		<mt-tab-item> 
			<span @click="onViewByJob">
				<font-awesome-icon icon="building" size="2x" />
			</span>
		</mt-tab-item>
		<mt-tab-item> 
			<span @click="onShowSearch">
				<font-awesome-icon icon="search" size="2x" />
			</span>
		</mt-tab-item>
	</mt-tabbar>
	</div>
</div>
</template>

<script>
import BizRCHub from '../../api/BizRCHub.js'
import Utils from '../../utils/index.js';
import HelperTimeSheet from '../../mixins/helperTimeSheet.js';

export default {
	name: 'RegionTimesheetList',
	mixins: [HelperTimeSheet],
	data(){
		return {
			listArray: [],
			listLevel: [],
			viewType: 0,
			manager: {},

			popupSearch:{
				regionList: [],
				region: '',
				visible: false
			},

			popupMenu:{
				visible: false
			}
		}
	},
	methods:{
		onShowSearch()
		{
			this.popupSearch.visible = true;
		},
		onSearch()
		{
			this.$showLoading();
			BizRCHub.GetRegionListByRegionName(this.popupSearch.region).then(res=>{
				if(res.data.OK)
				{
					this.popupSearch.regionList = res.data.Result;
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		onSearchCancel()
		{
			this.popupSearch.visible = false;
		},
		onUseRegion(regionPK, regionName)
		{
			this.popupSearch.visible = false;
			this.getList(regionPK, regionName);
		},	
		onLevelClick(item)
		{
			item.ShowChildren = ! item.ShowChildren;
			if(item.ShowChildren)
			{
				item.Icon = 'arrow-down';
			}
			else
			{
				item.Icon = 'arrow-right';
			}
		},
		onViewByDate()
		{
			this.viewType = 0;
			this.showView();
		},
		onViewByJob()
		{
			this.viewType = 1;
			this.showView();
		},
		showView()
		{
			if(this.viewType == 0)
			{
				this.listLevel = this.getViewByDate();
			}
			else
			{
				this.listLevel = this.getViewByJob();
			}
		},
		getList(regionPK, regionName)
		{
			if(!regionPK || !regionName)
			{
				return;
			}

			this.$showLoading();
			BizRCHub.GetRegionInvalidTimeSheetList(regionPK).then(res=>{
				if(res.data.OK)
				{
					this.listArray = res.data.Result;
					this.$store.commit('setRegionTimeSheetList', this.listArray);
					this.manager.SearchRegionPK = regionPK;
					this.manager.SearchRegionName = regionName;
					Utils.setUserSettings(this.manager);

					this.showView();
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		onShowMenu()
		{
			this.popupMenu.visible = true;
		},
		onJob()
		{
			this.$router.push({name: 'JobTimesheetList'});
		},
		onEquipmentRepair()
		{
			this.$router.push({name: 'EquipmentRepairList'});
		},
		onLogout()
		{
			Utils.logout();
			this.$router.push({ name: 'Login' });
		},
		onDetail(HRTimeSheetPK) {
			this.$router.push({name: 'RegionTimesheetDetail', params: {PK: HRTimeSheetPK}});
		},
		getViewByDate()
		{
			let map = new Map();
			for(let item of this.listArray)
			{
				let childMap;

				let keyDateTime = item.DateTime;
				if(!map.has(keyDateTime))
				{
					childMap = new Map();
					map.set(keyDateTime, childMap)
				}
				else
				{
					childMap = map.get(keyDateTime);
				}

				let keyJob = item.JobNumber;
				if(childMap.has(keyJob))
				{
					childMap.get(keyJob).push(item);
				}
				else
				{
					childMap.set(keyJob, [item]);
				}
			}

            let ret = [];
			for(let [key, value] of map)
			{
				let level0 = {};
				level0.LevelName = key;
				level0.ShowChildren = false;
				level0.Icon = 'arrow-right';
				level0.Children = [];
				ret.push(level0);

				for(let [k, v] of value)
				{
					let level1 = {};
					level1.LevelName = k;
					level1.ShowChildren = false;
					level1.Icon = 'arrow-right';
					level0.Children.push(level1);

					level1.Children = v;
				}
			}

			return ret;
		},
		getViewByJob()
		{
			let map = new Map();
			for(let item of this.listArray)
			{
				let childMap;

				let keyJob = item.JobNumber;
				if(!map.has(keyJob))
				{
					childMap = new Map();
					map.set(keyJob, childMap)
				}
				else
				{
					childMap = map.get(keyJob);
				}

				let keyDate = item.DateTime;
				if(childMap.has(keyDate))
				{
					childMap.get(keyDate).push(item);
				}
				else
				{
					childMap.set(keyDate, [item]);
				}
			}

            let ret = [];
			for(let [key, value] of map)
			{
				let level0 = {};
				level0.LevelName = key;
				level0.ShowChildren = false;
				level0.Icon = 'arrow-right';
				level0.Children = [];
				ret.push(level0);

				for(let [k, v] of value)
				{
					let level1 = {};
					level1.LevelName = k;
					level1.ShowChildren = false;
					level1.Icon = 'arrow-right';
					level0.Children.push(level1);

					level1.Children = v;
				}
			}

			return ret;
		}
	},
	mounted()
	{
		this.manager = Utils.getUserSettings();

		if(this.$store.state.regionTimeSheetList.length > 0)
		{
			this.listArray = this.$store.state.regionTimeSheetList;
			this.showView();
		}
		else
		{
			if(!this.manager.SearchRegionPK)
			{
				this.$showLoading();
				BizRCHub.GetRegionByJobNumber(this.manager.SearchJobNumber).then(res=>{
					if(res.data.OK)
					{
						let region = res.data.Result;
						this.manager.SearchRegionPK = region.CoRegionPK;
						this.manager.SearchRegionName = region.RegionName;
						this.getList(this.manager.SearchRegionPK, this.manager.SearchRegionName);
					}
					else
					{
						this.$alert(res.data.Error, 'Error');
						console.log(res.data.Error);
					}
					this.$closeLoading();
				}).catch(err=>{
					this.showNetworkError();
					console.log(err);
					this.$closeLoading();
				});
			}
			else
			{
				this.getList(this.manager.SearchRegionPK, this.manager.SearchRegionName);
			}
		}	
	}
}
</script>

<style scoped>
	.page-header {
		background-color: #a9362d;
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 100;
	}
	.mint-header {
		background-color: #a9362d;
	}
	.page-body{
		margin-bottom: 38px;
		margin-top: 40px;
	}
	.menu {
		width: 260px;
		height: 100%;
		background-color: #c45050;
	}
	ul{
    	list-style: none;
		color: #fff;
		padding: 20px;
	}
	ul li {
		padding: 20px 0;
		border-bottom: 1px solid rgba(255,255,255,0.2);
		transition: all ease 0.3s;
	}
	ul li:hover {
		border-bottom-color: #fff;
	}
	.menu svg {
		margin-right: 10px;
	}
	.level-0{
		text-align: left;
	}
	.level-0-title{
		float: left;
	}
	.level-0-icon{
		float: right;
	}
	.level-1{
		margin-left: 20px;
	}
	.level-1-title{
		float: left;
	}
	.level-1-icon{
		float: right;
	}
	.level-2{
		margin-left: 40px;
	}
	.mint-tabbar {
		position: static !important;
	}
	.mint-tabbar > .mint-tab-item {
		background-color: #a9362d;
		color: #fff;
	}
	.tabbar-wrapper {
		background-color: #a9362d;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
	}
	.dateTime,
	.postInOut {
		display: inline-block;
		margin-bottom: 6px;
		color: #8e8e93;
	}
	.checkInOut {
		display: inline-block;
		color: #8e8e93;
	}
	.checkInOut .svg-inline--fa,
	.postInOut .svg-inline--fa {
		margin-right: 10px;
	}
	.dateTime {
		font-weight: bolder;
		color:#c45050;
	}
	.page-body .functions {
		color:#c45050;
		margin-left: 8px;
		font-size: 1.1em;
	}
	.search {
		width: 80%;
		padding: 5px 10px 10px;
	}
	.mint-button--primary {
		background-color: #a9362d;
	}
	.button-group .mint-button--large {
		display: inline-block;
		width: 50%;
	}
	.level-1 {
		margin-left: 0;
	}
	.level-1 .mint-button {
		padding-left: 20px;
	}
	.level-2 {
		margin-left: 10px;
		padding: 10px 0;
		border-bottom: 1px solid #ddd;
	}
	.mint-cell, .mint-cell-wrapper {
		background-image: none;
	}
</style>
<style>
	/*.region-list .mintui {
		display: none;
	}*/
	.region-list .mint-cell,
	.region-list .mint-cell-wrapper {
		background-image: none;
	}	
</style>
